.mark {
    position: absolute;
    display: inline-block;
    height: calc(26px + 119px + 66px);
    @include hover {
        .mark__line {
            transform: translateY(0); } }
    &__circles {
        position: absolute;
        z-index: $zS;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%); }
    &__circle-inner {
        @include vcenter;
        @include circle(12);
        background-color: $green;
        z-index: $z-2; }
    &__circle-outer {
        @include vcenter;
        @include circle(26);
        z-index: $z-1;
        background-color: rgba($green, 0.3); }
    &__icon-circles {
        top: 0;
        left: 50%;
        transform: translateX(-50%); }
    &__icon-circle-inner {
        @include circle(50);
        @include vcenter;
        background-color: $green; }
    &__icon-circle-outer {
        @include circle(66);
        @include vcenter;
        background-color: rgba($green, 0.2); }
    &__icon {
        @include vcenter;
        font-size: 0;
        .icon {
            color: $white;
            font-size: 20px; } }
    &__line-wrapper {
        position: absolute;
        top: 0;
        bottom: 0;
        transform: translateX(-50%);
        left: 50%;
        height: 100%;
        width: 2px;
        overflow: hidden; }
    &__line-dashed {
        @include coverdiv;
        // width: 1px
        transform: translateY(100%);
        background-image: linear-gradient(to bottom, $green 50%,  transparent 50%);
        background-size: 3px 3px;
        z-index: $z-1; }
    &__line-solid {
        @include coverdiv;
        // width: 1px
        background-color: $green;

        transform: translateY(100%);
        z-index: $z-2; } }

