.slider {
    float: right;
    width: calc(50vw - 555px + 648px - 15px - 7.5px);

    .slick-list {
        padding-top: 20px; }
    &__slide {
        // max-height: 200px
        position: relative; }
    img {
        max-width: 100%; }
    &__btn {
        display: inline-block;
        position: absolute;
        top: 0;
        height: 50px;
        width: 50px;
        z-index: $zS;
        border: 1px solid $grey;
        transition: background-color $tr, border-color $tr;
        @include hover {
            background-color: $green;
            border-color: $green;
            .icon {
                color: $white; } }
        .icon {
            @include vcenter;
            transition: color $tr; }
        &_prev {
            left: 0; }
        &_next {
            left: 60px; } }
    &__text {
        margin-top: 100px;
        opacity: 0;
        transform: translateY(30px);
        transition: opacity $tr, transform $longTr;
        &.is-active {
            opacity: 1;
            transform: translateY(0); } }
    &__image {
        @include cover;
        padding-bottom: 630px; } }

