.features__slider:after {
  content: " ";
  display: table;
  clear: both;
}

.admin__images, .nomination__text {
  flex: 0 0 calc(50% - 555px + 462px + 15px);
  min-width: calc(50% - 555px + 462px + 15px);
}

.admin__text, .nomination__image {
  flex: 0 0 calc(50% - 555px + 648px - 15px);
  min-width: calc(50% - 555px + 648px - 15px);
}

.admin {
  display: flex;
}

.admin__images {
  position: relative;
  z-index: 10;
}

.admin__image-rect {
  padding-bottom: 80%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.admin__image-circle[data-anim-from] {
  position: absolute;
  top: 50%;
  right: 10%;
  transform: translateY(-50%);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50% 50%;
  height: 360px;
  width: 360px;
  border-radius: 50%;
  transition-delay: 1s;
}

.admin__image-circle[data-anim-from].is-active {
  position: absolute;
  transform: translateY(-50%);
}

.admin__text {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-left: 70px;
}

.nomination {
  display: flex;
}

.nomination__text {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  z-index: 2;
}

.nomination__text:after {
  position: absolute;
  top: 0;
  right: 0;
  transform-origin: 100% 0;
  height: calc(100% + 122px);
  width: 200%;
  transform: skew(23deg, 5.7deg);
  background-image: linear-gradient(-5.7deg, #5eb8e8 122px, #fff 122px);
  content: '';
  z-index: -1;
}

.nomination__image {
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  background-position: 100% 0;
  padding-bottom: 700px;
}

.processes {
  height: 2000px;
  background-image: url(../img/process_build.png), url(../img/process_bg.jpg);
  background-repeat: no-repeat;
  background-size: auto, contain;
  background-position: 100% 100%, 100% 100%;
}

.processes__main {
  position: relative;
  display: flex;
  flex-flow: column;
}

.processes__path {
  position: absolute;
  top: 0;
  left: 0;
}

.process {
  max-width: 470px;
  display: flex;
  position: relative;
  margin-bottom: 56px;
}

.process_right {
  margin-left: 285px;
}

.process__icons {
  position: relative;
  flex: 0 0 100px;
  min-width: 100px;
}

.process__circle-outer {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100px;
  width: 100px;
  border-radius: 50%;
  background-color: rgba(145, 200, 76, 0.3);
}

.process:last-child .process__circle-outer {
  background-color: red;
}

.process__circle-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 80px;
  width: 80px;
  border-radius: 50%;
  background-color: #91c84c;
}

.process__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  font-size: 0;
  z-index: 2;
}

.process__icon .icon {
  font-size: 30px;
}

.process__motion-el {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background-color: red;
  position: absolute;
  top: 40px;
  left: 40px;
}

.process__path {
  position: absolute;
  top: 50px;
  left: 50px;
}

.process_right .process__path {
  left: auto;
  right: 50px;
}

.process__path svg {
  fill-rule: evenodd;
  stroke: #91c84c;
  stroke-width: 1px;
  stroke-linecap: butt;
  stroke-linejoin: miter;
  opacity: 0.502;
  fill: none;
}

.process__path path {
  stroke-dasharray: 362px;
  stroke-dashoffset: 362px;
}

.process__text {
  margin-left: 30px;
}

.process__title {
  font-size: 20px;
  font-weight: 300;
  line-height: 1.7;
  letter-spacing: 1.6px;
  color: #202020;
}

.process__descr {
  font-size: 18px;
  font-weight: 300;
  line-height: 1.33;
  color: #a8a9ad;
}

.slider {
  float: right;
  width: calc(50vw - 555px + 648px - 15px - 7.5px);
}

.slider .slick-list {
  padding-top: 20px;
}

.slider__slide {
  position: relative;
}

.slider img {
  max-width: 100%;
}

.slider__btn {
  display: inline-block;
  position: absolute;
  top: 0;
  height: 50px;
  width: 50px;
  z-index: 10;
  border: 1px solid #a8a9ad;
  transition: background-color 0.4s, border-color 0.4s;
}

.no-touch .slider__btn:hover {
  background-color: #91c84c;
  border-color: #91c84c;
}

.no-touch .slider__btn:hover .icon {
  color: #fff;
}

.slider__btn .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: color 0.4s;
}

.slider__btn_prev {
  left: 0;
}

.slider__btn_next {
  left: 60px;
}

.slider__text {
  margin-top: 100px;
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.4s, transform 1s;
}

.slider__text.is-active {
  opacity: 1;
  transform: translateY(0);
}

.slider__image {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  padding-bottom: 630px;
}

[data-anim-from] {
  position: relative;
  opacity: 0;
  transition: opacity 1s, transform 1.2s;
}

[data-anim-from].is-active {
  transform: translate(0);
  opacity: 1;
}

[data-anim-from="bottom"] {
  transform: translateY(100px);
}

[data-anim-from="right"] {
  transform: translateX(50px);
}

[data-anim-from="left"] {
  transform: translateX(-50px);
}

[data-anim-from="opacity"] {
  opacity: 0;
}

[data-anim-from="opacity"].is-active {
  opacity: 1;
}

[data-anim-delay="0.3"] {
  animation-delay: 0.4s;
}

[data-circle-inner],
[data-circle-outer],
[data-icon-circle-inner],
[data-icon-circle-outer],
[data-icon],
[data-motion-el],
[data-numbers-list],
[data-concept-mask],
[data-title-number],
[data-title-rotated],
[data-title-line] {
  opacity: 0;
}

